import buildFormatLongFn from 'date-fns/locale/_lib/buildFormatLongFn/index.js'

var dateFormats = { full: 'EEEE, do MMMM y', long: 'do MMMM y', medium: 'd MMM y', short: 'dd.MM.yyyy' }
var timeFormats = { full: 'HH:mm:ss zzzz', long: 'HH:mm:ss z', medium: 'HH:mm:ss', short: 'HH:mm' }
var dateTimeFormats = { full: '{{date}} saat {{time}}', long: '{{date}} saat {{time}}', medium: '{{date}}, {{time}}', short: '{{date}}, {{time}}' }
var formatLong = {
    date: buildFormatLongFn({ formats: dateFormats, defaultWidth: 'full' }),
    time: buildFormatLongFn({ formats: timeFormats, defaultWidth: 'full' }),
    dateTime: buildFormatLongFn({ formats: dateTimeFormats, defaultWidth: 'full' })
}
export default formatLong